import React from "react";
import { Card, CardContent, Typography, Box, IconButton } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import { Link } from "react-router-dom";
import { URL } from "../../URL.js";
import CustomBTN from "../CustomButton/CustomBTN";
import LikeView from "../MarketplaceCard/LikeView.jsx";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
const MapModal = (props) => {
  const { filterState, modalC } = props;
  const handleClose = () => {
    modalC(false);
  };
  let likesList = filterState?.likesList;
  let favoritesList = filterState?.favoritesList;
  return (
    <>
      <Box
        sx={{
          height: { xs: "425px", md: "490px" },
          width: { xs: "290px", md: "365px" },
          borderRadius: "15px",
          position: "absolute",
          backgroundColor: "background.secondary",
          top: "80px",
          left: { xs: "-14%", md: "0%" },
        }}
      >
        <Box sx={{ position: "relative", borderRadius: "15px" }}>
          <Box sx={{ position: "absolute", top: "10px", right: "10px" }}>
            <IconButton onClick={handleClose}>
              <CloseIcon
                sx={{
                  fontSize: "30px",
                  color: "white",
                  background:
                    "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                  "&:hover": {
                    background:
                      "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                  },
                  opacity: "",
                  padding: "8px",
                  borderRadius: "10px",
                }}
              />
            </IconButton>
          </Box>
          {/* <Box sx={{ position: "absolute", bottom: "10px", left: "10px" }}>
            <Link to="/" style={{ color: "white" }}>
              <MyLocationIcon
                sx={{
                  fontSize: "30px",
                  color: "white",
                  backgroundColor: "#6e07c7",
                  opacity: "",
                  padding: "8px",
                  borderRadius: "10px",
                }}
              />
            </Link>
          </Box> */}
          {/* <Box sx={{ position: "absolute", bottom: "10px", right: "10px" }}>
            <LandModal handleClose={handleClose} />
          </Box> */}
          <Box
            sx={(theme) => ({
              boxShadow:
                theme.palette.mode === "light"
                  ? "rgb(242,231,231) 10px 10px 20px 22px inset, rgb(242,231,231) 10px 18px 36px 28px inset"
                  : "rgb(21 0 47 / 60%) 10px 10px 20px 22px inset, rgb(21 0 47 / 60%) 10px 18px 36px 28px inset",
              backgroundImage: `url(${URL}/upload/${filterState?.capturesImage})`,
              backgroundPosition: "center",
              backgroundSize: "cover",
              borderRadius: "10px",
              height: { xs: "160px", md: "215px" },
              width: "100%",
            })}
          >
            {/* <Box sx={{ width: "100%", height: { xs: "160px", md: "230px" } }}>
              <video
                src={`${URL}/upload/${props?.filterState?.file}`}
                width="500"
                height="300"
                controls="controls"
                autoplay="true"
              />
            </Box> */}
          </Box>
        </Box>

        <CardContent sx={{ m: { xs: 0 } }}>
          <Typography gutterBottom sx={{ pb: 2, fontSize: { xs: "15px" } }}>
            {filterState?.name}
          </Typography>
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <Typography sx={{ fontSize: { xs: "15px" } }}> Price</Typography>
            <Typography component="p" sx={{ fontSize: { xs: "15px" } }}>
              {filterState?.productTotalPrice + "$"}
            </Typography>
          </Box>
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <Typography sx={{ fontSize: { xs: "15px" } }}>
              {" "}
              Lat / Lng
            </Typography>
            <Typography component="p" sx={{ fontSize: { xs: "15px" } }}>
              55.3231 , 25.5454
              {/* {filterState?.productTotalPrice + "$"} */}
            </Typography>
          </Box>
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <Typography
              sx={{ fontSize: "1rem", color: "gray", fontWeight: 700 }}
            >
              Auther
            </Typography>

            <Box
              sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                color: "gray",
              }}
            >
              <Typography
                component="p"
                sx={{
                  fontSize: "10px",
                  mr: 1,
                  maxWidth: "120px",
                  overflow: "hidden",
                }}
              >
                Join Smith
              </Typography>

              <Box>
                <AccountCircleIcon
                  style={{
                    width: "35px",
                    height: "35px",
                    borderRadius: "50%",
                  }}
                />
              </Box>
            </Box>
          </Box>

          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "centers",
            }}
          >
            <Typography
              component="p"
              sx={{ fontSize: "1rem", color: "gray", fontWeight: 700 }}
            >
              status
            </Typography>
            <Typography
              color={filterState?.paidStatusContract ? "green" : "red"}
              component="p"
              sx={{ fontSize: "12px", ml: 1, mt: 1, fontWeight: 700 }}
            >
              {filterState?.paidStatusContract ? "Mint succes" : "pending"}
            </Typography>
          </Box>

          <LikeView
            userlikeviews={{ likesList, favoritesList }}
            howManyViews={filterState?.howManyViews}
          />

          <Link
            to={`/nftDetail/${filterState?._id}`}
            style={{ textDecoration: "none", marginBottom: "20px" }}
          >
            <CustomBTN text="View Details" />
          </Link>
        </CardContent>
      </Box>
    </>
  );
};

export default React.memo(MapModal);
