import React, { useEffect, useRef, useState } from "react";
import maplibregl from "maplibre-gl";
import * as MaplibreGrid from "maplibre-grid";
import "maplibre-gl/dist/maplibre-gl.css";
import MapboxGeocoder from "@mapbox/mapbox-gl-geocoder";
import "@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css";
import mapboxgl from "mapbox-gl";
import { getBuyCells, payWithWallet, getSinglePromo } from "../Api/api";
import CustomBTN from "./CustomButton/CustomBTN";
import { useParams } from "react-router-dom";
import "./Bmap.css";
import { Box, Stack, Typography, Tooltip, Divider, Paper } from "@mui/material";
import MyLocationIcon from "@mui/icons-material/MyLocation";
import PlaceIcon from "@mui/icons-material/Place";
import ContactSupportIcon from "@mui/icons-material/ContactSupport";
import LanguageIcon from "@mui/icons-material/Language";
import CloseIcon from "@mui/icons-material/Close";
import VisibilityIcon from "@mui/icons-material/Visibility";
import BrushIcon from "@mui/icons-material/Brush";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import cardbg from "../images/cardbg.png";
import cardbgDark from "../images/cardbgDark.png";
import {
  useSendTransaction,
  usePrepareSendTransaction,
  useContractRead,
  useBalance,
} from "wagmi";
import { parseUnits } from "@ethersproject/units";
import MapModal from "./modal/MapModal";
//.....Redux react.. .......
import { AddCeil } from "../Redux/Reducer";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import Checkout from "../stripe/stripe";
import usdAbi from "../Connectivityas/ABI/usdContractAbi.json";
import { usdContract } from "../Connectivityas/Environement";
import Loading from "../Loading/Loading";
import { useAccount } from "wagmi";

import AdminModal from "../Component/modal/adminMint";
// import reserveBoxModal from "../";
function Bmap() {
  const params = useParams();
  const usenavigate = useNavigate();
  const dispatch = useDispatch();
  const { ceilSlice } = useSelector((state) => state);
  const { userdetails } = ceilSlice;
  // const { address } = useContext(WalletContextProvider);
  const { address } = useAccount();
  const [dataValue, setData] = useState(0);
  const mapContainer = useRef(null);
  const screenshotRef = useRef(null);
  const map = useRef(null);
  const [dataLenght, setDataLenght] = useState(0);
  const [tilePrice, setTilePrice] = useState(1);
  const [totalCeil, setTotalCeil] = useState([]);
  const [modalvalue, setModalValue] = useState(false);
  const [filterState, setFilterstate] = useState("");
  const [paymentCard, setpaymentCard] = useState(false);
  const [payment, setpayment] = useState(false);
  const [paymentWallet, setpaymentWallet] = useState(true);
  const [cardBtn, setCardBtn] = useState(true);
  const [ceilDetailsstate, setCeilDetailState] = useState("");
  const [open, setOpen] = useState(false);
  const [bLoading, setBLoading] = useState(false);
  const [removeCurrentCeil, setRemoveceil] = useState(false);
  const [loadCeil, setLoadCeil] = useState("");
  const [discountValue, setDiscountValue] = React.useState(0);
  const [openModal, setOpenModal] = React.useState(false);
  const [backbtn, setBackBtn] = useState(false);

  const [boxCenter, setBoxCenter] = useState([
    73.1083603962225, 31.42117432409888,
  ]);
  const [promoValue, setPromoValue] = useState("");
  const filterRole =
    userdetails.role && userdetails?.role.filter((role) => role !== null);
  let lat, lng;
  let select = false;
  let selectedCells = [];
  let fixedDataValue = parseFloat(dataValue).toFixed(6);
  const parseValue = parseUnits(fixedDataValue.toString(), 6);
  const {
    data: newData,
    isError: readEror,
    isLoading: loadingBnb,
  } = useContractRead({
    address: usdContract,
    abi: usdAbi,
    functionName: "usdtToBNB",
    args: [parseValue],
  });
  const { data: get_balance } = useBalance({
    address: address,
  });
  let balance = get_balance && +get_balance?.formatted;
  const { config } = usePrepareSendTransaction({
    request: {
      to: "0xbb17CDC95b626c76E934Df7bF05fEaEFcEa9C61E",
      value: newData,
    },
    onError(data) {
      setBLoading(false);
    },
  });
  const { data, isLoading, isError, isSuccess, sendTransaction } =
    useSendTransaction(config);
  const init = React.useCallback(async () => {
    if (data && !openModal) {
      payWithWallet(data, isError, ceilDetailsstate, +parseValue, setBLoading);
    }
  }, [data, isLoading, isError, isSuccess]);
  React.useEffect(() => {
    init();
  }, [init]);

  //......end contract here........
  const closehandleClick = () => {
    setDataLenght(0);
    setRemoveceil(!removeCurrentCeil);
  };
  useEffect(() => {
    let len = totalCeil.length;
    if (len) {
      setModalValue(false);
    }
    const totalPrice = len * tilePrice;
    setDataLenght(len);
    setData(totalPrice);
  }, [totalCeil]);
  //...........> end the fetching data <............
  const mapMethod = () => {
    selectedCells = [];
    mapboxgl.accessToken =
      "pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4M29iazA2Z2gycXA4N2pmbDZmangifQ.-g_vE53SD2WrJ6tFX7QHmA";
    if (map.current) return;
    map.current = new mapboxgl.Map({
      container: mapContainer.current,
      style: "mapbox://styles/mapbox/satellite-streets-v12",
      center: [55.266833632327774, 25.20345319338018],
      zoom: 16.5,
      scrollZoom: true,
      preserveDrawingBuffer: true,
    });

    const geocoder = new MapboxGeocoder({
      accessToken: mapboxgl.accessToken,
      mapboxgl: mapboxgl,
    });
    document
      .getElementById("geocoder")
      .appendChild(geocoder.onAdd(map.current));
    // MaplibreGrid?.GRID_CLICK_EVENT=='mousemove';

    map.current.on("load", () => {
      const grid1 = new MaplibreGrid.Grid({
        gridWidth: 0.02,
        gridHeight: 0.02,
        units: "kilometers",
        minZoom: 15.5,
        maxZoom: 22,
        active: false,
        paint: {
          "line-opacity": 0.5,
          "line-color": "white",
        },
      });

      map.current.addControl(
        new mapboxgl.GeolocateControl({
          positionOptions: {
            enableHighAccuracy: true,
          },
          trackUserLocation: true,
        })
      );
      map.current.addControl(
        new maplibregl.NavigationControl({
          showZoom: true,
        })
      );
      map.current.addControl(grid1);
      // --------------------> Map handler  function <-----------------
      const maploadHandle = async () => {
        const selectedCellsId = "selected-cells";
        map.current.addSource(selectedCellsId, {
          type: "geojson",
          data: { type: "FeatureCollection", features: selectedCells },
        });
        //------------------------> add layer 1 <---------------------------
        map.current.addLayer({
          id: selectedCellsId,
          source: selectedCellsId,
          active: true,
          type: "fill",
          paint: {
            "fill-color": "#0000ff",
            "fill-opacity": 0.2,
            "fill-outline-color": "red",
          },
        });
        //---------------> Get selected ceill  & Data <-----------------
        map.current.on("grid.click", (event) => {
          const bbox = event.bbox;
          const cellIndex = selectedCells.findIndex(
            (x) => x.geometry.bbox.toString() === bbox.toString()
          );
          if (cellIndex === -1 && select !== true) {
            const coordinates = [
              [
                [bbox[0], bbox[1]],
                [bbox[2], bbox[1]],
                [bbox[2], bbox[3]],
                [bbox[0], bbox[3]],
                [bbox[0], bbox[1]],
              ],
            ];
            const cell = {
              type: "Feature",
              geometry: { type: "Polygon", bbox, coordinates },
            };
            selectedCells.push(cell);
            setTotalCeil((totalCeil) => [...totalCeil, cell]);
          } else {
            if (!select) {
              selectedCells.splice(cellIndex, 1);
              setTotalCeil((totalCeil) =>
                totalCeil.filter((item, i) => i !== cellIndex)
              );
            } else {
              // toast.error("This land is already selected !");
              setModalValue(true);
            }
          }
          const source = map.current.getSource(selectedCellsId);
          source.setData({
            type: "FeatureCollection",
            features: selectedCells,
          });
          setpaymentCard(false);
          setLoadCeil("");
        });
        //------------------------> 2nd layer <----------------------
        setBLoading(true);
        const selectedCellsOldId = "selected-Oldcells";
        let buyceilValue = await getBuyCells();
        dispatch(AddCeil(buyceilValue?.nfts));
        buyceilValue = buyceilValue?.nfts;
        setBLoading(false);
        let allUserData = [];
        for (let i = 0; i < buyceilValue?.length; i++) {
          for (let j = 0; j < buyceilValue[i]?.attributes.length; j++) {
            allUserData.push(buyceilValue[i]?.attributes[j]);
          }
        }
        //.....filter map ceil ...........
        map.current.on("grid.click", (event) => {
          console.log("event........>", event);
          const bbox = event.bbox;
          let result = buyceilValue?.find((element) => {
            return element?.attributes.find((item) => {
              return (
                JSON.stringify(bbox) === JSON.stringify(item.geometry.bbox)
              );
            });
          });
          if (result !== "") {
            setFilterstate(result);
          }
        });

        map.current.on("grid.click", () => {
          setpaymentCard(false);
        });
        //........All filter data end.........
        map.current.addSource(selectedCellsOldId, {
          type: "geojson",
          data: { type: "FeatureCollection", features: allUserData },
        });
        map.current.addLayer({
          id: "selectedCellsOldId",
          source: selectedCellsOldId,
          active: true,
          type: "fill",
          paint: {
            "fill-color": "white",
            "fill-opacity": 0.3,
            "fill-outline-color": "blue",
          },
        });
      };
      map.current.on("load", maploadHandle());
      map.current.on("mouseenter", "selectedCellsOldId", () => {
        select = true;
        map.current.getCanvas().style.cursor = "not-allowed";
      });
      map.current.on("mouseleave", "selectedCellsOldId", () => {
        select = false;
        map.current.getCanvas().style.cursor = "";
      });
      const grid1GuiConfig = { enabled: true };
      const updateGrid = (grid, gridGuiConfig) => {
        if (gridGuiConfig.enabled) {
          if (!map.current.hasControl(grid)) {
            map.addControl(grid);
          } else {
            grid.update();
          }
        } else {
          if (map.current.hasControl(grid)) {
            map.current.removeControl(grid);
          }
        }
      };
      updateGrid(grid1, grid1GuiConfig);
    });
  };

  useEffect(() => {
    mapMethod();
  }, [boxCenter, removeCurrentCeil]);

  useEffect(() => {
    if (!!params?.lang && !!params?.long) {
      map.current.flyTo({
        center: [params?.long, params?.lang],
      });
    }
  }, [params]);

  // .............Buy Now handler..............
  const zoomsetting = async () => {
    const mediumCeil = totalCeil.length / 2;
    const getMediumCeilNumber = Math.floor(mediumCeil);
    lat = totalCeil[getMediumCeilNumber]?.geometry?.bbox[0];
    lng = totalCeil[getMediumCeilNumber]?.geometry?.bbox[1];

    if (totalCeil.length < 30) {
      await map.current.flyTo({
        center: [lat, lng],
        zoom:
          totalCeil?.length > 9
            ? totalCeil?.length < 18
              ? 17
              : totalCeil?.length < 25
              ? 16.7
              : 16.5
            : 18,
      });
    }
  };
  const zoomOutHandle = async () => {
    await map.current.flyTo({ zoom: 16.5 });
  };
  const buyMap = async () => {
    try {
      if (!userdetails?.id) {
        usenavigate("/login");
        return false;
      }
      if (address && userdetails?.id) {
        zoomsetting();
        setOpen(true);
        setTimeout(() => {
          var snapshot = document.querySelector("#snapshot");
          var ctx = snapshot.getContext("2d");
          var png = map.current.getCanvas().toDataURL();
          var scale = window.devicePixelRatio;
          var halfWidth = window.innerWidth * scale;
          var windowHeight = window.innerHeight * scale;
          // resize canvas after iframe is loaded and account for device pixel ratio
          snapshot.height = windowHeight;
          snapshot.width = halfWidth;
          var copy = new Image();
          copy.src = png;
          copy.onload = function () {
            ctx.drawImage(copy, 0, 0);
          };
          const obj = {
            totalceil: totalCeil,
            snapshot: png,
            user: userdetails?.id,
            tilePrice: tilePrice,
            address: address,
            lang: lng,
            long: lat,
            discounted: discountValue,
          };
          console.log("obj....>", obj);
          setCeilDetailState(obj);
          setLoadCeil(obj);
          zoomOutHandle();
        }, 2000);
      } else {
        toast.error("Please connect Wallet !");
      }
    } catch (e) {
      console.log("Error In buyCell process ", e);
    }
  };
  const openPaymentMethod = async () => {
    setpayment(!payment);
  };
  //...........Modal close ............
  const modalHandle = (value) => {
    setModalValue(value);
  };
  const applyPromosDiscount = async () => {
    if (promoValue) {
      const res = await getSinglePromo(promoValue);
      const data = res?.data?.data;
      if (data) {
        const discount = Number(data.offers);
        // const discountedValue = Math.round((dataValue / 100) * discount);
        const discountedValue = (dataValue / 100) * discount;
        setData(dataValue - discountedValue);
        setDiscountValue(discountedValue);
        document.querySelector(".promoInputSection").style.display = "none";
        return toast.success("Discount Code applied");
      } else {
        return toast.error("This Promo is not working");
      }
    } else {
      return toast.error("Please Provide Valid Promo Code");
    }
  };
  const hideShowPromo = () => {
    document.querySelector(".promoInputField").style.display = "none";
    document.querySelector(".promoInputSection").style.display = "flex";
  };
  const handleChange = (e) => {
    setPromoValue(e.target.value);
  };

  const openLandModal = async () => {
    buyMap();
    setOpenModal(true);
  };

  return (
    <>
      <Loading loading={bLoading} />
      {ceilDetailsstate && openModal ? (
        <AdminModal
          handleClose={setOpenModal}
          openModal={openModal}
          ceilDetailsstate={ceilDetailsstate}
          setBLoading={setBLoading}
          dataValue={dataValue}
        />
      ) : (
        ""
      )}
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          maxWidth: "xl",
        }}
      >
        <div id="screenshot" ref={screenshotRef}>
          <Box
            sx={{
              height: "100vh",
              marginRight: "0px",
              mt: 0,

              position: "relative",
            }}
            ref={mapContainer}
          />
        </div>

        <Paper
          sx={{
            position: "absolute",

            top: { xs: "60px", md: "120px" },
            zIndex: "1",
            right: { xs: "30%", md: "6%" },
            width: { xs: "200px", md: "350px" },
            borderRadius: "15px",
            padding: { xs: "0px 0px", md: "4px 8px" },
            backgroundColor: "background.secondary",
            backdropFilter: "blur(1px)",
          }}
        >
          <Box
            sx={{
              p: { md: 1 },
              display: { xs: "flex", md: "flex" },
              justifyContent: "flex-start",
              alignItems: "center",
            }}
          >
            <div id="geocoder" className="geocoder"></div>
            <Box sx={{ display: { xs: "none", md: "block" } }}>
              <Stack spacing={2} direction="row">
                <LanguageIcon sx={{ ml: 1 }} />
                <VisibilityIcon />
              </Stack>
            </Box>
          </Box>
          {dataLenght && modalvalue !== true ? (
            <Box
              className="mapcontainer"
              id="PaymentBox"
              sx={(theme) => ({
                backgroundImage:
                  theme.palette.mode === "light"
                    ? `url(${cardbg})`
                    : `url(${cardbgDark})`,
                backgroundSize: "100% 100%",
                position: "absolute",
                top: { xs: "70px", md: "90px" },
                left: { xs: "0%", md: "0" },
                zIndex: " -0.9",
                width: { xs: "240px", md: "350px" },
                height: { xs: "300px", md: "400px" },
                borderRadius: "15px",
                padding: { xs: "0px 0px", md: "4px 8px" },
              })}
            >
              <Box
                p={2}
                ml={3}
                sx={{
                  height: { xs: "245px", md: "305px" },
                  overflowY: "scroll",
                  backdropFilter: "blur(1px)",
                  scrollbarColor: " rebeccapurple green",
                  scrollbarWidth: "thin",
                  mt: { xs: 4, md: 6 },
                }}
              >
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "space-between",
                  }}
                >
                  <Typography
                    sx={{
                      fontWeight: "bold",
                      fontSize: { xs: "14px", md: "25px" },
                    }}
                  >
                    FREE LAND
                  </Typography>
                  <Box>
                    <Stack spacing={2} direction="row">
                      <MyLocationIcon
                        sx={{
                          fontSize: { xs: "25px", md: "30px" },
                          color: "white",
                          background:
                            "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                          padding: { xs: "4px", md: "6px" },
                          borderRadius: "10px",
                        }}
                      />
                      <CloseIcon
                        onClick={closehandleClick}
                        sx={{
                          fontSize: { xs: "25px", md: "30px" },
                          color: "white",
                          cursor: "pointer",
                          background:
                            "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                          padding: { xs: "4px", md: "6px" },
                          borderRadius: "10px",
                        }}
                      />
                    </Stack>
                  </Box>
                </Box>

                <Box
                  mt={1}
                  sx={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                  }}
                >
                  <Stack
                    spacing={1}
                    direction="row"
                    sx={{
                      display: "flex",
                      alignItems: "center",
                    }}
                  >
                    <BrushIcon />
                    <Typography
                      sx={{
                        color: "text.secondary",
                        fontSize: { xs: "10px", md: "20px" },
                      }}
                    >
                      NOT LAND ART{" "}
                    </Typography>
                  </Stack>
                  <Tooltip title="You can only create land art on this map, only non Urban and water tiles for land">
                    <ContactSupportIcon sx={{ color: "text.secondary" }} />
                  </Tooltip>
                </Box>
                <Box
                  mt={2}
                  sx={{
                    display: "flex",
                    justifyContent: "space-between",
                  }}
                >
                  <Stack
                    spacing={2}
                    direction="row"
                    sx={{
                      display: "flex",
                      alignItems: "center",
                    }}
                  >
                    <PlaceIcon />
                    <Typography sx={{ color: "text.secondary" }}>--</Typography>
                  </Stack>
                  <Tooltip title="You can only create land art on this map, only non Urban and water tiles for land">
                    <ContactSupportIcon sx={{ color: "text.secondary" }} />
                  </Tooltip>
                </Box>
                <Stack spacing={1} sx={{ mt: { xs: 1, md: 2 } }}>
                  <Box sx={{ ml: { xs: 0, md: 0 } }}>
                    <Typography
                      component="h2"
                      sx={{
                        display: "flex",
                        justifyContent: "space-between",
                        fontSize: { xs: "12px", md: "30px" },
                      }}
                    >
                      $ value
                      <Typography
                        component="span"
                        sx={{
                          fontSize: {
                            xs: "12px",
                            md: "30px",
                          },
                        }}
                      >
                        {" "}
                        ${dataValue?.toFixed(1)}
                      </Typography>
                    </Typography>
                  </Box>
                  <Box
                    sx={{
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center",
                      color: "#848484",
                    }}
                  >
                    <Typography
                      variant="body1"
                      color="text.secondary"
                      sx={{
                        fontSize: { xs: "10px", md: "20px" },
                      }}
                    >
                      Current value per tile
                    </Typography>
                    <Typography
                      component="span"
                      sx={{
                        fontSize: { xs: "10px", md: "20px" },
                        color: "text.secondary",
                      }}
                    >
                      $1.32
                    </Typography>
                  </Box>
                </Stack>

                {paymentCard ? (
                  <Box
                    sx={{
                      pb: 2,
                    }}
                  >
                    <Typography sx={{ fontSize: "1rem", width: "100%" }}>
                      Pay with Debit Card
                    </Typography>
                    <Checkout
                      ceil={ceilDetailsstate}
                      setBLoading={setBLoading}
                      loadCeil={loadCeil}
                      discountValue={discountValue}
                    />
                    <Typography variant="p">
                      Payment processed by Wert, USD price is for indication
                      only. 4% transaction fee will be included. Min $5
                      purchases.
                    </Typography>
                  </Box>
                ) : (
                  ""
                )}
                {payment ? (
                  <>
                    <Box>
                      {paymentWallet ? (
                        <>
                          <Box
                            sx={{
                              display: "flex",
                              mt: 1,
                            }}
                          >
                            <Typography
                              sx={{
                                fontSize: { xs: "10px", md: "15px" },
                                width: "100%",
                              }}
                            >
                              Pay with Wallet
                            </Typography>
                            <Typography
                              sx={{ fontSize: { xs: "10px", md: "15px" } }}
                            >
                              {address?.slice(0, 4) +
                                "..." +
                                address?.slice(-4)}
                            </Typography>
                          </Box>
                          <Box
                            sx={{ width: "100%" }}
                            onClick={async () => {
                              setBackBtn(true);
                              if (+balance <= 0)
                                return toast.error("Insufficient funds");
                              setCardBtn(false);
                              await buyMap();
                              await sendTransaction();
                            }}
                          >
                            <CustomBTN text="Pay with wallet" />
                          </Box>
                        </>
                      ) : (
                        ""
                      )}

                      {cardBtn ? (
                        <Box>
                          <Box mt={1}>
                            <Typography
                              sx={{
                                fontSize: { xs: "10px", md: "15px" },
                                color: "text.primery",
                              }}
                            >
                              Pay with Card
                            </Typography>
                          </Box>
                          <Box
                            onClick={async () => {
                              setBackBtn(true);
                              setpaymentCard(!paymentCard);
                              setpaymentWallet(!paymentWallet);

                              await buyMap();
                            }}
                            sx={{ width: "100%" }}
                          >
                            <CustomBTN text="Pay with Debit Card" />
                          </Box>
                        </Box>
                      ) : (
                        ""
                      )}
                    </Box>
                    <Box
                      sx={{
                        display: "flex",
                        flexDirection: { xs: "column", md: "row" },
                        justifyContent: "space-between",
                        marginTop: "1rem",
                      }}
                    >
                      <Box
                        sx={{
                          display: "inline-block",
                          color: "#fff",
                          fontSize: {
                            xs: filterRole?.includes("admin") ? "10px" : "12px",
                            md: filterRole?.includes("admin") ? "11px" : "15px",
                          },
                          fontWeight: "bold",
                          marginTop: "10px",
                          textAlign: "center",
                          background:
                            "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                          padding: "10px 10px",
                          borderRadius: "10px",
                          cursor: "pointer",
                          letterSpacing: "1px",
                          width: {
                            xs: filterRole?.includes("admin") ? "100%" : "100%",
                            md: filterRole?.includes("admin") ? "40%" : "100%",
                          },
                        }}
                        onClick={hideShowPromo}
                        className="promoInputField"
                      >
                        Add Promo +
                      </Box>
                      {filterRole?.includes("admin") ? (
                        <Box
                          onClick={openLandModal}
                          sx={{
                            display: "inline-block",
                            color: "#fff",
                            fontSize: { xs: "10px", md: "11px" },
                            fontWeight: "bold",
                            textAlign: "center",
                            marginTop: "10px",
                            background:
                              "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                            padding: "10px 10px",
                            borderRadius: "10px",
                            cursor: "pointer",
                            letterSpacing: "1px",
                          }}
                          className="promoInputField"
                        >
                          Mint Nft to Specific
                        </Box>
                      ) : (
                        ""
                      )}
                    </Box>
                    <div
                      style={{
                        width: "100%",
                        height: "42px",
                        marginTop: "10px",
                        display: "none",
                        justifyContent: "space-between",
                      }}
                      className="promoInputSection"
                    >
                      <input
                        style={{
                          width: "67%",
                          height: "100%",
                          fontSize: "20px",
                          borderRadius: "10px",
                          outline: "none",
                          border: "1px solid #dad7cd",
                          padding: "0 4px",
                          placeholder: "Promo Code",
                        }}
                        value={promoValue}
                        onChange={handleChange}
                        type="input"
                      />
                      <Box
                        sx={{
                          width: "30%",
                          height: "100%",
                          background:
                            "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                          display: "flex",
                          justifyContent: "center",
                          alignItems: "center",
                          borderRadius: "10px",
                          fontSize: { xs: "10px", md: "15px" },
                          color: "white",
                          fontWeight: 500,
                          cursor: "pointer",
                        }}
                        onClick={applyPromosDiscount}
                      >
                        APPLY
                      </Box>
                    </div>
                    <br />
                    {backbtn ? (
                      <Box
                        sx={{ mt: -1, cursor: "pointer" }}
                        onClick={() => {
                          setCardBtn(true);
                          setpaymentWallet(true);
                          setpaymentCard(false);
                        }}
                      >
                        <CustomBTN className="checkBtn" text="Go back" />
                      </Box>
                    ) : (
                      ""
                    )}

                    <Divider
                      color="white"
                      height="5px"
                      style={{ marginTop: "10px" }}
                    />
                    <Box
                      sx={{
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                        marginTop: "7px",
                      }}
                    >
                      <Typography sx={{ fontSize: "1rem", fontWeight: 900 }}>
                        Total
                      </Typography>

                      <Typography sx={{ fontSize: "1rem", fontWeight: 900 }}>
                        {dataValue ? dataValue?.toFixed(2) : ""}
                      </Typography>
                    </Box>
                  </>
                ) : undefined}
                <br />
                {userdetails?.id ? (
                  <Box>
                    {!payment ? (
                      <Box
                        sx={{ mt: -2, cursor: "pointer" }}
                        onClick={() => {
                          const bValue = [];
                          totalCeil.forEach((el) => {
                            bValue.push(JSON.stringify(el.geometry.bbox));
                          });

                          address
                            ? openPaymentMethod()
                            : toast.error("connect with wallet");
                        }}
                      >
                        <CustomBTN className="checkBtn" text="CHECK OUT" />
                      </Box>
                    ) : (
                      ""
                    )}
                  </Box>
                ) : (
                  <Link
                    to="/login"
                    style={{ textDecoration: "none", color: "white" }}
                  >
                    <CustomBTN text="Login" />
                  </Link>
                )}
              </Box>
            </Box>
          ) : (
            <>
              {modalvalue === true ? (
                ""
              ) : (
                <Paper
                  sx={{
                    position: "absolute",
                    top: { xs: "70px", md: "90px" },
                    left: { xs: "0%", md: "0" },
                    zIndex: " -0.9",
                    width: { xs: "240px", md: "350px" },
                    borderRadius: { xs: "5px", md: "10px" },
                    padding: { xs: "4px 4px", md: "10px 8px" },
                    backdropFilter: "blur(1px)",
                    backgroundColor: "background.secondary",
                  }}
                >
                  <Box>
                    <Typography sx={{ pl: 1 }}>No selected Tile</Typography>
                  </Box>
                </Paper>
              )}

              {modalvalue && (
                <MapModal modalC={modalHandle} filterState={filterState} />
              )}
            </>
          )}
        </Paper>
      </Box>
      <div style={{ width: "100%", display: "none" }}>
        <canvas id="snapshot" style={{ width: "100%" }}></canvas>
      </div>
    </>
  );
}

export default Bmap;
